.data-filters {
  background: ghostwhite;
  padding: 5px 20px;
  border: black solid 1px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.data-filters label,
.data-filters button {
  margin: 0px 3px;
  font-family: sans-serif;
}

.data-filters .toggle-label {
  margin-left: 1px;
}

.data-filters .buffer {
  font-size: 32px;
  color: lightgray;
  margin-left: 3px;
  margin-right: 3px;
}

/* Credit to W3 Schools documentation https://www.w3schools.com/howto/howto_css_switch.asp */
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 17px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 13px;
  width: 13px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked+.slider {
  background-color: #2196F3;
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(13px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
